.chatInputField {
  padding: 15px;
  width: 80%;
  outline: none;
  font-size: 1rem;
  font-family: poppins;
  border: none;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  transition: all 0.3s ease;
  font-weight: bold;
}
.chatInputField:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}
.chatInputField:focus {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

.ChattingForm {
  width: 90%;
  position: Absolute;
  bottom: 2%;
  left: 10%;
}

.sendButton {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  outline: none;
  color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  margin-left: 10px;
  cursor: pointer;
  background-color: #0f80fe;
  transition: 0.3s all ease;
}
.sendButton:hover {
  transform: rotate(360deg);
}
.sendButton:disabled {
  box-shadow: none;
  color: #0f80fe;
  background-color: white;
  cursor: none;
  transform: rotate(0deg);
}
.chatContainer {
  max-height: 80vh;
  padding: 10px;
  overflow-y: auto;
  width: 100%;
}
.chatContainer::-webkit-scrollbar {
  width: 0px;
}
.chatContainer::-webkit-scrollbar-track-piece {
  background-color: white;
}
.chatLeftItem {
  display: flex;
  min-height: 40px;
  font-family: poppins;
  font-size: 17px;
  font-weight: bold;
  word-wrap: break-word;
  word-break: break-all;
  margin-right: 10;
  height: auto;
  justify-content: flex-start;
}
.chatRightItem {
  display: flex;
  word-wrap: break-word;
  word-break: break-all;
  min-height: 40px;
  margin-left: 10;
  font-weight: bold;
  font-family: poppins;
  font-size: 17px;
  height: auto;
  justify-content: flex-end;
}

@keyframes rotateAndGlow {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    opacity: 1;
    transform: rotate(360deg);
  }
}

.seen {
  animation: rotateAndGlow 1s ease;
}

.parentChatContainer {
  position: relative;
  height: 90vh;
}

@media (max-width: 1000px) and (min-width: 770px) {
  .parentChatContainer {
    height: 85vh;
  }
  .chatContainer {
    max-height: 75vh;
  }
}
@media all and (max-width: 767px) {
  .parentChatContainer {
    height: 90vh;
  }
  .ChattingForm {
    bottom: 2%;
  }
  .chatContainer {
    max-height: 80vh;
  }
}
.topDetails {
  padding: 10px;
}
.topDetails:hover {
  background: rgba(0, 0, 0, 0.05);

  height: 100%;
  transition: all 1s ease;
}
