.MySelectField {
  padding: 10px;
  width: 300px;
  border-radius: 10px;

  outline: none;
  border: none;
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.1);
  transition: 0.5s all ease;
}
.MySelectField:focus {
  background-color: white;
  cursor: none;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
}
.MySelectField:disabled {
  background-color: rgba(0, 0, 0, 0.04);
  color: lightgray;
}
